<template>
  <div class="router-main login-main">
    <div class="login">
      <h2>
        登录
        <span></span>
      </h2>
      <el-form
        class="loginform"
        :model="form"
        :rules="rules"
        label-width="60px"
        ref="form"
        size="medium"
      >
        <el-form-item label="手机号" prop="username">
          <el-input placeholder="手机号" v-model.trim="form.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input type="password" placeholder="密码" v-model.trim="form.password"></el-input>
        </el-form-item>
        <el-form-item label="验证码" prop="code" class="code">
          <el-input
            placeholder="请输入验证码"
            v-model.trim="form.code"
            @keydown.enter.native="submitHandle"
          ></el-input>
          <div class="img" v-html="src" @click="setSrc"></div>
        </el-form-item>
        <el-form-item>
          <el-button type="success" size="medium" style="width:100%;" @click="submitHandle">登录</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      src: '',
      form: {
        username: '',
        password: '',
        code: ''
      },
      rules: {
        username: [
          {
            validator: this.validateUsername,
            trigger: 'blur',
            message: '请填写您的手机号'
          }
        ],
        password: [
          {
            validator: this.validatePassword,
            trigger: 'blur',
            message: '请填写6-20位的密码'
          }
        ],
        code: [
          {
            validator: this.validateCode,
            message: '请填写4位的验证码',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  methods: {
    submitNative() {
      return false;
    },
    validateUsername(rule, value, callback) {
      const len = value.length;
      if (len != 11) {
        callback(new Error('请填写您的手机号'));
      } else {
        callback();
      }
    },
    validatePassword(rule, value, callback) {
      const len = value.length;
      if (len > 20 || len < 6) {
        callback(new Error('请填写密码'));
      } else {
        callback();
      }
    },
    validateCode(rule, value, callback) {
      const len = value.length;
      if (len == 0 || len > 4) {
        callback(new Error('请填位验证码'));
      } else {
        callback();
      }
    },
    async setSrc() {
      const doc = await this.$tools.httpAgent('/plugin/captcha', 'get', {
        1: 1
      });
      this.src = doc.data.captcha;
      localStorage.setItem('captcha', doc.data.captchaid);
    },
    submitHandle: async function () {
      // const self = this;
      this.$refs.form.validate(async valid => {
        if (valid) {
          this.form.captcha = localStorage.getItem('captcha');
          const doc = await this.$tools.httpAgent('/login', 'post', this.form, {
            login: false
          });
          if (doc.code === 200) {
            localStorage.setItem('jwt', JSON.stringify(doc.data));
            this.$router.push('/usercenter');
          } else {
            // self.$layer.msg('帐号或者密码错误');
            this.setSrc();
          }
        }
      });
    }
  },
  mounted() {
    localStorage.clear();
    this.setSrc();
  }
}
</script>

<style lang="less" scoped>
.login-main {
  background: linear-gradient(to right, #6041bf, #6076f3);
  padding: 100px 0;
  .login {
    width: 600px;
    margin: 0 auto;
    h2 {
      text-align: center;
      margin-bottom: 100px;
      font-size: 32px;
      color: #fff;
      position: relative;
      span {
        display: inline-block;
        height: 0;
        width: 100px;
        border-top: 3px solid #2ee599;
        position: absolute;
        bottom: -29px;
        left: 50%;
        margin-left: -50px;
      }
    }
    .loginform {
      width: 100%;
      min-height: 300px;
      border-radius: 15px;
      background-color: #fff;
      padding: 50px 30px;
    }
  }
  .code /deep/ .el-form-item__content {
    display: flex;
    .img {
      width: 120px;
      height: 40px;
      margin: 0px 2px 0 15px;
      cursor: pointer;
      svg {
        width: 100%;
        height: 100%;
      }
    }
    .el-input {
      flex: 1;
    }
  }
}
</style>

